import Vue from 'vue'  // vue
import VueRouter from 'vue-router' // vue-router
Vue.use(VueRouter)
// 1. 
import Designer from '@/views/designer/index'
import Preview from '@/views/preview/index'
// 2.  router  `routes` 
export default new VueRouter({
    // 3. 
    // component Vue.extend() 
    // 
    routes: [
        {
            path: '/',
            name: 'Designer',
            component: Designer
        }, {
            path: '/preview',
            name: 'preview',
            component: Preview
        }
    ]
})
