<template>
  <div>
    <el-form label-width="80px" size="mini">
      <el-form-item label="">
        <el-input v-model="option.text"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-color-picker v-model="option.textColor" show-alpha></el-color-picker>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="option.fontWeight">
          <el-option label="" value="lighter"/>
          <el-option label="" value="normal"/>
          <el-option label="" value="bold"/>
          <el-option label="()" value="bolder"/>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="option.fontStyle">
          <el-option label="" value="normal"/>
          <el-option label="" value="italic"/>
          <el-option label="" value="oblique"/>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="option.textFamily">
          <el-option v-for="(item,index) in textFamilyList" :key="index+item" :label="item" :value="item"/>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-input-number :min="13" :max="200" v-model="option.textSize" style="width: 100%"/>
      </el-form-item>
      <el-form-item label="">
        <el-input-number :min="13" :max="250" v-model="option.textLineHeight" style="width: 100%"/>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="option.textAlign">
          <el-option v-for="(item,index) in textAlignList" :key="index+item" :label="item" :value="item"/>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="option.textDecoration">
          <el-option v-for="(item,index) in textDecorationList" :key="index+item.label" :label="item.label" :value="item.value"/>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="option.url"></el-input>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: "cpt-text-option",
  props: {
    option: Object
  },
  data() {
    return {
      textFamilyList: ['', '', '', '','',''],
      textAlignList: ['left', 'center', 'right'],
      textDecorationList: [{label:'',value:'none'},{label:'',value:'underline'},{label:'',value:'overline'},
        {label:'',value:'line-through'},{label:'()',value:'blink'}]
    }
  }
}
</script>

